.b-resource-assignment-cell {
    padding-block         : 0.4em;

    --b-avatar-background : var(--b-resource-assignment-column-avatar-background, var(--b-primary-90));
}

.b-resource-avatar-container {
    display     : flex;
    align-items : center;
    height      : 100%;

    .b-resource-avatar {
        margin-inline-end : 2px;
        height            : 100%;
        width             : auto;

        &.b-drag-original {
            opacity : .5;
        }
    }

    &:empty {
        height : 2.2em;
    }
}

.b-draggable-resource-avatars .b-resource-avatar-container > .b-resource-avatar {
    cursor : pointer;
}

.b-overflow-img {
    overflow : hidden;
    position : relative;

    .b-resource-avatar {
        filter            : brightness(60%);
        margin-inline-end : 0;
    }

    .b-overflow-count {
        position       : absolute;
        left           : 50%;
        top            : 50%;
        transform      : translate(-50%, -50%);
        color          : #fff;
        pointer-events : none;
    }
}
