/* @after Gantt/widget/AssignmentGrid.css */

.b-assignment-picker {
    --b-panel-padding                : 0;
    --b-menu-padding                 : 0;
    --b-panel-bottom-toolbar-padding : var(--b-widget-padding);

    .b-assignment-grid {
        flex : 1 1 auto;
    }

    .b-bottom-toolbar {
        border-top : 1px solid var(--b-grid-cell-border-color);

        .b-toolbar-content {
            justify-content : flex-end;

            > button {
                min-width : 8em;
            }
        }
    }
}
