.b-timeline-overlay-chart {
    position       : absolute;
    pointer-events : none;

    .b-timeline-chart-axis {
        stroke       : black;
        stroke-width : 1px;
        opacity      : 0.2;
    }

    .b-timeline-chart-path {
        fill         : none;
        stroke       : red;
        stroke-width : 2px;
    }

    .b-timeline-chart-point {
        pointer-events : all;
        fill           : red;
        r              : 3;
        transition     : r .3s;

        &:hover {
            r : 5;
        }
    }
}
