    svg.b-sch-dependencies-canvas {
        .b-sch-dependency:not(.b-click-area) {
            marker-end   : none;
            marker-start : var(--b-dependency-marker);

            &.b-sch-dependency-markerless,
            &.b-sch-dependency-ends-outside {
                marker-start : none;
            }
        }
    }
}

