.b-assignment-grid {
    --b-grid-cell-padding-inline                               : 0.5em;

    /* Looks cleaner with same color in the checkbox selection column */
    --b-grid-cell-checkbox-selection-background                : var(--b-grid-cell-background);
}

.b-assignment-grid-resource-column {
    &.b-grid-header {
        padding-block : var(--b-grid-header-padding);
    }

    .b-filter-bar-field {
        margin-bottom : 0;
    }

    .b-grid-header-text {
        display : none;
    }
}

.b-resource-assignment-cell {
    .b-assignment-chip-view-wrap {
        /*
         * This will constrain the ChipView's width so that the ChipView itself will scroll.
         */
        overflow : hidden;
    }
}

